import React from "react";
import css from "./style.module.css";

const Order = (props) => {
  return (
    <div className={css.Order}>
      <p>
         :   : {props.order.orts.bacon},  :{" "}
        {props.order.orts.salad},   : {props.order.orts.meat},  :
        {props.order.orts.cheese}
      </p>
      <p>
         : {props.order.hayag.name} | {props.order.hayag.street} |{" "}
        {props.order.hayag.city}
      </p>
      <p>
          : <strong>{props.order.dun}</strong>
      </p>
    </div>
  );
};

export default Order;
